import React, { Component } from "react";
import Fade from "react-reveal/Fade";
import Slide from "react-reveal/Slide";
import MyButton from "../utils/MyButton";
export default class Descount extends Component {
  state = {
    discountStart: 0,
    discountEnd: 30,
  };

  porcentage = () => {
    if (this.state.discountStart < this.state.discountEnd) {
      this.setState({
        discountStart: this.state.discountStart + 1,
      });
    }
  };

  componentDidUpdate() {
    setTimeout(() => {
      this.porcentage();
    }, 30);
  }

  render() {
    return (
      <div className="center_wrapper">
        <div className="discount_wrapper">
          <Fade onReveal={() => this.porcentage()}>
            <div className="discount_porcentage">
              <span>{this.state.discountStart}%</span>
              <span>OFF</span>
            </div>
          </Fade>
          <Slide right>
            <div className="discount_description">
              <h3>purchase tickets before 20th MAY</h3>
              <p>
                See the documentation below for a complete reference to all of
                the props and classes available to the components mentioned
                here.
              </p>

              <MyButton
                text="Purchased tickets"
                bck="#ffa800"
                color="white"
                link="https://google.com"
              />
            </div>
          </Slide>
        </div>
      </div>
    );
  }
}
